
import { defaultCssPlugin,cssPluginOptionsExtra } from "../adapter/css";
import { defaultHtmlPlugin ,htmlPluginOptionsExtra } from "../adapter/html";
import {PLUGIN_NEED_NAME,handlerError} from '../options/error'
// plugin{
//     name:'service worker',
