import histroyDara from './histroy'
import { initOwlMetricInst,getOwlMetricInst } from './owl';
function request(params) {
  const inst=getOwlMetricInst()
  inst.setExtraData(JSON.stringify(params))
    inst.setMetric(inst.raptorKey,1)
      inst.report()
}

export async function requestMessage(data) {
  try {
    if(!navigator.onLine){
      histroyDara.addHistroyData({
        ...data,
        errorCode:3000
      })
      return
    }
    await request(data);
  } catch (e) {
    console.log(e)
  }
}

export function initRequest(vm) {
  if (
    vm.$options &&
    vm.$options.request &&
    typeof vm.$options.request == "function"
  ) {
    vm.requestMessage = vm.$options.request;
  }else{
    initOwlMetricInst(vm)
    vm.requestMessage = requestMessage;
  }
  histroyDara.useHistroyData(vm.requestMessage)
}



