import Owl from '@dp/owl'
let metricInst=null
export function initOwlMetricInst(vm){
    if(metricInst){
        return metricInst
    }
    const {$options}=vm
    window.
    Owl.start($options.owlOptions);
    metricInst =Owl.MetricManager();
    metricInst.raptorKey=$options.raptorKey
    return metricInst
}
export function getOwlMetricInst(){
    if(metricInst){
        return metricInst
    }
    return initOwlMetricInst()
}